import * as React from 'react';
import { useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import MobileStepper from '@mui/material/MobileStepper';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import KeyboardArrowLeft from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRight from '@mui/icons-material/KeyboardArrowRight';
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';
import Layout from '../components/Layout';
import img1 from './offices/img1.webp';
import img2 from './offices/img2.webp';
import img3 from './offices/img3.webp';
import img4 from './offices/img4.webp';
import img5 from './offices/img5.webp';
import img6 from './offices/img6.webp';
import img7 from './offices/img7.webp';
import img8 from './offices/img8.webp';
import img9 from './offices/img9.webp';
import img10 from './offices/img10.webp';
import img11 from './offices/img11.webp';
import img12 from './offices/img12.webp';
import img13 from './offices/img13.webp';
import img14 from './offices/img14.webp';
const AutoPlaySwipeableViews = autoPlay(SwipeableViews);
import GoogleMapReact from 'google-map-react';
const images = [
  {
    label: 'E 61st Street',
    imgPath:img1
  },
  {
    label: 'E 61st Street Surgery Room',
    imgPath:img2
  },
  {
    label: '65 N Broadway',
    imgPath:img3
  },
  {
    label: '65 N Broadway Surgery Room',
    imgPath:img4
  },
  {
    label: '74 Street Brooklyn',
    imgPath:img5
  },
  {
    label: '74 Street Brooklyn Surgery Room',
    imgPath:img6
  },
  {
    label: '235 E 22nd',
    imgPath:img7
  },
  {
    label: '235 E 22nd Surgery Room',
    imgPath:img8
    
  },
  {
    label: '86-22 Bay Parkway',
    imgPath:img9
    
  },
  {
    label: '86-22 Bay Parkway Surgery Room',
    imgPath:img10
    
  },
  {
    label: '140 N Paramus',
    imgPath:img11
    
  },
  {
    label: '140 N Paramus Surgery Room',
    imgPath:img12
    
  },
  {
    label: '39 W Herald Square',
    imgPath:img13
    
  },
  {
    label: '39 W Herald Square Surgery Room',
    imgPath:img14
    
  }
];


  const defaultProps = {
    center: {
      lat:40.63105196371259,
      lng:-74.02424743681289
    },
    zoom:15
  };

function SwipeableTextMobileStepper() {
  const theme = useTheme();
  const [activeStep, setActiveStep] = React.useState(0);
  const maxSteps = images.length;

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStepChange = (step: number) => {
    setActiveStep(step);
  };
  return (
    <Layout fullMenu>
    <article id="main">
      <header>
        <h2>Office Gallery</h2>
        <p>Here you can see the location of the offices and their photos.</p>
      </header>
      <section className="wrapper style5 style-map-img">
      <div className="google-map-api" style={{height:'37.5vw', width: '40%'}}>
        <GoogleMapReact
          passive={true}
          bootstrapURLKeys={{ key: "" }}
          defaultCenter={defaultProps.center}
          defaultZoom={defaultProps.zoom}
        >
        {/* <AnyReactComponent
            {lat=59.955413};
            {lng=30.337844};
            {text="My Marker"};
        /> */}
        </GoogleMapReact>
      </div>  
      <Box className="root-slider" sx={{ flexGrow: 1 }}>
        <Paper
          square
          elevation={0}
          sx={{
            display: 'flex',
            alignItems: 'center',
            height: 50,
            pl: 2,
            bgcolor: 'background.default',
          }}
        >
          <h4>{images[activeStep].label}</h4>
        </Paper>
        <AutoPlaySwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={activeStep}
          onChangeIndex={handleStepChange}
          enableMouseEvents
          
        >
          {images.map((step, index) => (
            <div key={step.label}>
              {Math.abs(activeStep - index) <= 2 ? (
                <Box
                  component="img"
                  sx={{
                    display: 'block',
                    overflow: 'hidden',
                    width: '100%',
                  }}
                  src={step.imgPath}
                  alt={step.label}
                />
              ) : null}
            </div>
          ))}
        </AutoPlaySwipeableViews>
        <MobileStepper
          steps={maxSteps}
          position="static"
          activeStep={activeStep}
          nextButton={
            <Button
              className='next-btn'
              size="small"
              onClick={handleNext}
              disabled={activeStep === maxSteps - 1}
            >
              Next
              {theme.direction === 'rtl' ? (
                <KeyboardArrowLeft />
              ) : (
                <KeyboardArrowRight />
              )}
            </Button>
          }
          backButton={
            <Button size="small"
            onClick={handleBack}
            disabled={activeStep === 0}
            className='back-btn'
            >
              {theme.direction === 'rtl' ? (
                <KeyboardArrowRight />
              ) : (
                <KeyboardArrowLeft />
              )}
              Back
            </Button>
          }
        />
      </Box>   
      </section>
    </article>
  </Layout>
  );
}

export default SwipeableTextMobileStepper;
